body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

.App-logo {
    height: 5rem;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-header h1 {
    font-size: 2em;
    margin: 0;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

main {
    width: 100%;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.members-table {
    margin-left: auto;
    margin-right: auto;
    border-collapse: collapse;
}

.members-table thead {
    font-weight: bold;
    font-size: 1.5rem;
    text-align: left;
}

.members-table th,
.members-table td {
    padding: 1rem 1.5rem;
}

.member-row {
    font-size: 1.2rem;
    /* font-weight: bold; */
}

.animals-cell {
    display: flex;
    min-height: 100%;
}

.animals-cell div {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 1rem;
    font-size: 0.75rem;
}

.name-cell {
    font-weight: bold;
}

.member-row:nth-child(odd) {
    background-color: #efefef;
}

.member-row img {
    border-radius: 50%;
}

.avatar-cell img {
    width: 5rem;
    height: 5rem;
}  